import * as React from 'react';
import { render } from 'react-dom';
import {
  Card,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  TextInput,
  TextLink,
  Icon,
  IconButton
} from '@contentful/forma-36-react-components';
import { init, FieldExtensionSDK } from 'contentful-ui-extensions-sdk';
import '@contentful/forma-36-react-components/dist/styles.css';
import './index.scss';

interface Props {
  sdk: FieldExtensionSDK;
}

interface State {
  table?: any;
}

export class App extends React.Component<Props, State> {
  state = {
    table: {
      head: [] as string[],
      body: [] as string[][]
    }
  };

  constructor(props: Props) {
    super(props);
  }

  detachExternalChangeHandler: Function | null = null;

  componentDidMount() {
    const { sdk } = this.props;
    sdk.window.startAutoResizer();
    this.onValueChange();
  }

  componentWillMount() {
    // Get columns amount from parameters and create empty array
    const { sdk } = this.props;
    const columns = sdk.parameters.instance.columns || 6;
    const emptyRow = new Array(columns).fill('');
    let emptyTable = { head: emptyRow, body: [] };

    // Update state to empty table with header colum amount from parameters
    if (this.state.table.head.length === 0) {
      this.setState({ emptyTable });
    }
  }

  onValueChange = async () => {
    // Get columns amount from parameters and create empty array
    const { sdk } = this.props;
    const columns = sdk.parameters.instance.columns || 6;
    const emptyRow = new Array(columns).fill('');
    let emptyTable = { head: emptyRow, body: [] };

    // Update state with value on change
    sdk.field.onValueChanged((value: object) => {
      this.setState({
        table: value ? value : emptyTable
      });
    });
  };

  componentWillUnmount() {
    if (this.detachExternalChangeHandler) {
      this.detachExternalChangeHandler();
    }
  }

  updateTable = (
    event: React.ChangeEvent<HTMLInputElement>,
    group: string,
    cellIndex: number,
    rowIndex?: number
  ) => {
    const { sdk } = this.props;
    const cell = event.currentTarget.value;
    let table = { ...this.state.table };

    // Update table row cell
    if (group === 'head') {
      table[group][cellIndex] = cell || '';
    }
    if (group === 'body') {
      table[group][rowIndex][cellIndex] = cell || '';
    }

    sdk.field.setValue({ ...table }).catch(error => {
      // console.log(error);
      sdk.notifier.success(`Something went wrong. Try again.`);
    });
  };

  addRow = () => {
    const { sdk } = this.props;
    let table = { ...this.state.table };

    // Get columns amount from parameters and create empty array
    const columns = sdk.parameters.instance.columns || 6;
    const emptyRow = new Array(columns).fill('');

    // Add empty row to table
    table.body.push(emptyRow);

    sdk.field.setValue({ ...table }).catch(error => {
      // console.log(error);
      sdk.notifier.success(`Something went wrong. Try again.`);
    });
  };

  removeRow = (rowIndex: number) => {
    const { sdk } = this.props;
    let table = { ...this.state.table };

    // Remove selected row
    table.body.splice(rowIndex);

    sdk.field.setValue({ ...table }).catch(error => {
      // console.log(error);
      sdk.notifier.success(`Something went wrong. Try again.`);
    });
  };

  render = () => {
    const { table } = this.state;
    const { head, body } = table;

    return (
      <div
        style={{
          width: '100%'
        }}>
        {table && (
          <Card
            style={{
              marginBottom: '.875rem'
            }}>
            <Table testId="cf-ui-table">
              {head && head.length > 0 && (
                <TableHead>
                  <TableRow>
                    {head.map((cell, cellIndex) => (
                      <TableCell key={`head-${cellIndex}`} align="left">
                        <TextInput
                          type="text"
                          value={cell}
                          onChange={event => this.updateTable(event, 'head', cellIndex)}
                        />
                      </TableCell>
                    ))}
                    <TableCell align="right" />
                  </TableRow>
                </TableHead>
              )}
              <TableBody>
                {body &&
                  body.length > 0 &&
                  body.map((row, rowIndex) => (
                    <TableRow key={`body-${rowIndex}`}>
                      {row &&
                        row.length > 0 &&
                        row.map((cell, cellIndex) => (
                          <TableCell key={`body-${cellIndex}`} align="left">
                            <TextInput
                              key={`body-${cellIndex}`}
                              type="text"
                              value={cell}
                              onChange={event =>
                                this.updateTable(event, 'body', cellIndex, rowIndex)
                              }
                            />
                          </TableCell>
                        ))}
                      <TableCell
                        align="right"
                        style={{
                          verticalAlign: 'middle'
                        }}>
                        <IconButton
                          buttonType="muted"
                          onClick={() => this.removeRow(rowIndex)}
                          label="Remove row"
                          iconProps={{
                            icon: 'Close'
                          }}
                          style={{
                            marginTop: '8px'
                          }}
                        />
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
            <div className="actionBar inside">
              <TextLink onClick={() => this.addRow()}>
                <Icon
                  color="primary"
                  icon="Plus"
                  size="small"
                  style={{
                    verticalAlign: 'middle',
                    marginRight: '4px',
                    marginBottom: '2px'
                  }}
                />
                Add row
              </TextLink>
            </div>
          </Card>
        )}
      </div>
    );
  };
}

init(sdk => {
  render(<App sdk={sdk as FieldExtensionSDK} />, document.getElementById('root'));
});

